import { useEffect, useState } from 'react'

const DEFAULT_DATA = {
    "shrimp": { count: 0, totalAxn: 0, addresses: [] },
    "crab": { count: 0, totalAxn: 0, addresses: [] },
    "fish": { count: 0, totalAxn: 0, addresses: [] },
    "octopus": { count: 0, totalAxn: 0, addresses: [] },
    "dolphin": { count: 0, totalAxn: 0, addresses: [] },
    "tigerShark": { count: 0, totalAxn: 0, addresses: [] },
    "greatWhite": { count: 0, totalAxn: 0, addresses: [] },
    "whale": { count: 0, totalAxn: 0, addresses: [] },
    "totals": { held_axn: 0, holders: 0, last_updated: 0, addresses: [] },
    "addresses": []
}

const useHolderData = () => {
    const [data, setData] = useState(null);

    const [bothData, setBothData] = useState(null);
    const [stakingData, setStakingData] = useState(null);
    const [circulatingData, setCirculatingData] = useState(null);

    const _fetchInfo = () => {
        fetch(`eco/totals`).then(result => {
            result.json().then(res => {
                setData(res.combined_ecosystem)
                setBothData(res.combined_ecosystem);
                setStakingData(res.staking_ecosystem);
                setCirculatingData(res.liquid_ecosystem);
            })
        })
        .catch(err => console.log(err))
    }

    useEffect(() => {
        _fetchInfo()    
    }, [])

    const changeData = selected => {
        if(selected.liquid && selected.staked)
            setData(bothData)
        else if (selected.liquid)
            setData(circulatingData)
        else if (selected.staked)
            setData(stakingData)
        else 
            setData({...DEFAULT_DATA})
    }

    return {
        data,
        changeData
    }
}


export default useHolderData;